    margin: 0 0.5rem 0.5rem 0;
    margin-left: 0 !important;
    border: none;
    transition: none;
    box-shadow: 0 0 0 1px #eeeeee !important;
    filter: grayscale(70%); }
    .alert-types button span:before {
      opacity: 0.1;
      color: #000; }
      .alert-types button span:before:hover, .alert-types button span:before:focus, .alert-types button span:before:active {
        fill: #000; }
    .alert-types button.btn-active {
      filter: grayscale(0);
      box-shadow: 0 0 0 1px #000000 !important; }
      .alert-types button.btn-active span:before {
        opacity: 1; }
    .alert-types button.alert-dark {
      box-shadow: 0 0 0 1px #555555 !important;
      border: none !important;
      filter: brightness(1.4) grayscale(70%); }
      .alert-types button.alert-dark span:before {
        color: #888888; }
      .alert-types button.alert-dark.btn-active {
        filter: brightness(1.6) grayscale(0); }
        .alert-types button.alert-dark.btn-active span:before {
